import React from "react";
import KeyBoard from "./KeyBoard";

class Select extends React.Component {
  constructor() {
    super();
    this.state = {
      size: 10,
      mode: 1,
    };
    this.handleSizeChange = this.handleSizeChange.bind(this);
    this.handleModeChange = this.handleModeChange.bind(this);
  }

  handleSizeChange = (e) => {
    this.setState({
      size: e.target.value,
    });
  };

  handleModeChange = (e) => {
    this.setState({
      mode: e.target.value,
    });
  };

  render() {
    return (
      <div>
        <h3>迷路オプション</h3>
        <ul className="list">
          <li>
            迷路サイズ:{" "}
            <select className="selectSize" onChange={this.handleSizeChange}>
              <option value="">--サイズ選択--</option>
              <option value="10">10 × 10</option>
              <option value="20">20 × 20</option>
              <option value="30">30 × 30</option>
              <option value="40">40 × 40</option>
              <option value="50">50 × 50</option>
            </select>
          </li>
          <li>
            モード選択:{" "}
            <select className="selectMode" onChange={this.handleModeChange}>
              <option value="">--モードを選択--</option>
              <option value="1">到達モード ⇨ モード1</option>
              <option value="2">最長モード　→　モード2</option>
            </select>
          </li>
        </ul>
        <br />
        <KeyBoard size={this.state.size} mode={this.state.mode} />
      </div>
    );
  }
}

export default Select;
