import React from "react";

const rowStyle = {
  height: 12,
};

const roadStyle = {
  width: 12,
  height: 12,
  border: "solid 1px gray",
  display: "inline-block",
  boxsizing: "border-box",
  lineheight: "12px",
};

const X = 0;
const Y = 1;

class Show extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const data = this.props.data;
    const answerData = this.props.answer;
    console.log(answerData);

    const movePoint = (rowIndex, colIndex) => {
      if (rowIndex === this.props.xIndex && colIndex === this.props.yIndex) {
        return true;
      }
      return false;
    };

    const goalPoint = (rowIndex, colIndex) => {
      if (rowIndex === this.props.goal[X] && colIndex === this.props.goal[Y]) {
        return true;
      }
      return false;
    };

    const showAnswer = (rowIndex, colIndex, answerShow) => {
      if (answerShow === 0) {
        return goalPoint(rowIndex, colIndex) ? true : false;
      }
      // 迷路を再生成したときに情報が行ってないかも?
      for (
        let answerIndex = 0;
        answerIndex < answerData.length;
        answerIndex++
      ) {
        if (
          answerData[answerIndex][X] === rowIndex &&
          answerData[answerIndex][Y] === colIndex
        ) {
          return true;
        }
      }
      return false;
    };

    return (
      <div className="Show">
        <div>
          {data.map((row, rowIndex) => {
            return (
              <div style={rowStyle} key={`row:${rowIndex}`}>
                {row.map((point, colIndex) => {
                  // road はtrue or false
                  const road = point;
                  return (
                    <button
                      key={`row:${rowIndex},col:${colIndex}`}
                      className={movePoint(rowIndex, colIndex) ? "move" : ""}
                      // スタイルの実装を考え直せ
                      style={{
                        ...roadStyle,
                        backgroundColor: road
                          ? showAnswer(
                              rowIndex,
                              colIndex,
                              this.props.answerShow
                            )
                            ? "skyblue"
                            : ""
                          : "black",
                      }}
                    ></button>
                  );
                })}
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default Show;
