import React from "react";
import Timer from "./Timer";
import Maze from "./Maze";
import MazeAnswer from "./MazeAnswer";

const ON = 1;
const OFF = 0;

const REACHMODE = 1;
const LONGESTMODE = 2;

const X = 0;
const Y = 1;

class KeyBoard extends React.Component {
  constructor(props) {
    super(props);
    const maze = new Maze(this.props.size);
    const mazeData = maze.digGoal(this.props.mode);
    const answer = new MazeAnswer(mazeData, this.props.size);
    const answerData = answer.getAnswerRoute(this.props.mode);
    const mazeGoal = [answerData.slice(-1)[0][X], answerData.slice(-1)[0][Y]];
    this.state = {
      data: mazeData,
      answer: answerData,
      answerShow: OFF,
      reset: 0,
      xIndex: this.props.size - 1,
      yIndex: 1,
      movable: 0,
      goal:
        this.props.mode === REACHMODE
          ? [0, this.props.size - 2]
          : [mazeGoal[X], mazeGoal[Y]],
    };
    this.handleShowMazeAnswer = this.handleShowMazeAnswer.bind(this);
    this.handleResetMaze = this.handleResetMaze.bind(this);
    this.keyDownAction = this.keyDownAction.bind(this);
  }

  handleShowMazeAnswer = () => {
    if (this.state.answerShow === ON) {
      this.setState({
        answerShow: OFF,
      });
      return;
    }
    this.setState({
      answerShow: ON,
    });
  };

  handleResetMaze = () => {
    const newMaze = new Maze(this.props.size);
    const newMazeData = newMaze.digGoal(this.props.mode);
    const newAnswer = new MazeAnswer(newMazeData, this.props.size);
    const newAnswerData = newAnswer.getAnswerRoute(this.props.mode);
    const newMazeGoal = () => {
      if (Number(this.props.mode) === REACHMODE) {
        return [0, this.props.size - 2];
      }
      return [newAnswerData.slice(-1)[0][X], newAnswerData.slice(-1)[0][Y]];
    };

    this.setState({
      data: newMazeData,
      answer: newAnswerData,
      answerShow: OFF,
      xIndex: this.props.size - 1,
      yIndex: 1,
      goal: newMazeGoal(),
    });
  };

  getReset = () => {
    if (this.state.reset === 1) {
      return 0;
    }
    if (this.state.reset === 0) {
      return 0;
    }
  };

  keyDownAction = (e) => {
    let direction = [];
    if (this.state.movable !== 1) {
      if (e.key === "ArrowUp" && this.state.xIndex > 0) {
        direction = [-1, 0];
      } else if (
        e.key === "ArrowRight" &&
        this.state.yIndex < this.props.size - 1
      ) {
        direction = [0, 1];
      } else if (
        e.key === "ArrowDown" &&
        this.state.xIndex < this.props.size - 1
      ) {
        direction = [1, 0];
      } else if (e.key === "ArrowLeft" && this.state.yIndex > 0) {
        direction = [0, -1];
      } else {
        direction = [0, 0];
      }
      const nextXPoint = this.state.xIndex + direction[0];
      const nextYPoint = this.state.yIndex + direction[1];
      let nextPoint = [];
      if (this.state.data[nextXPoint][nextYPoint]) {
        nextPoint = [nextXPoint, nextYPoint];
      } else if (!this.state.data[nextXPoint][nextYPoint]) {
        nextPoint = [this.state.xIndex, this.state.yIndex];
      }
      this.setState({
        xIndex: nextPoint[0],
        yIndex: nextPoint[1],
      });
    }
    if (
      this.state.xIndex === this.state.goal[X] &&
      this.state.yIndex === this.state.goal[Y]
    ) {
      this.setState({
        movable: 1,
      });
    }
  };

  componentDidMount = () => {
    document.addEventListener("keydown", this.keyDownAction);
  };
  componentWillUnmount = () => {
    document.removeEventListener("keydown", this.keyDownAction);
  };

  render() {
    return (
      <div className="keyboard">
        <button className="resetbutton" onClick={this.handleResetMaze}>
          作成
        </button>
        <Timer
          data={this.state.data}
          answer={this.state.answer}
          answerShow={this.state.answerShow}
          size={this.props.size}
          xIndex={this.state.xIndex}
          yIndex={this.state.yIndex}
          goal={this.state.goal}
          reset={this.state.reset}
        />
        <h4>
          モード {this.props.mode}
          <br />
          迷路サイズは {this.props.size} × {this.props.size} です
        </h4>
        <button className="answerbutton" onClick={this.handleShowMazeAnswer}>
          迷路の解答
        </button>
      </div>
    );
  }
}

export default KeyBoard;
