import React from "react";
import Show from "./Show";

class Timer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hours: "00",
      minutes: "00",
      seconds: "00",
      time: 0,
      timer: "startingPoint",
    };
    this.timerUse = this.timerUse.bind(this);
  }

  start = () => {
    if (this.state.timer !== "stop") {
      this.timer = setInterval(() => this.update(), 1000);
      this.setState({
        timer: "start",
      });
    }
  };

  stop = () => {
    clearInterval(this.timer);
    if (this.state.timer === "pause") {
      this.setState({
        timer: "start",
      });
      return;
    }
    if (this.state.timer !== "stop") {
      this.setState({
        timer: "pause",
      });
    }
  };

  reset = () => {
    this.setState({
      hours: "00",
      minutes: "00",
      seconds: "00",
      time: 0,
    });
  };

  update = () => {
    const time = this.state.time + 1;
    const hours = parseInt(time / 60 / 60, 10);
    const minutes = parseInt((time / 60) % 60, 10);
    const seconds = parseInt(time % 60, 10);
    this.setState({
      hours: this.toText(hours),
      minutes: this.toText(minutes),
      seconds: this.toText(seconds),
      time: time,
    });
  };

  toText = (time) => {
    return ("00" + time).slice(-2);
  };

  timerUse = () => {
    if (this.props.xIndex === 0) {
      this.stop();
      this.setState({
        timer: "stop",
      });
    }
  };

  componentDidMount = () => {
    document.addEventListener("keydown", this.timerUse);
  };
  componentWillUnmount = () => {
    document.removeEventListener("keydown", this.timerUse);
  };

  render() {
    return (
      <div>
        <h5>〈〈 Timer 〉〉</h5>
        <button className="startbutton" onClick={this.start}>
          START
        </button>
        <button className="stopbutton" onClick={this.stop}>
          STOP
        </button>
        <button className="resetbutton" onClick={this.handleResetMaze}>
          RESET
        </button>
        <div id="showTimer">
          <h5>
            {this.state.hours} : {this.state.minutes} : {this.state.seconds}
          </h5>
        </div>
        <Show
          data={this.props.data}
          answer={this.props.answer}
          answerShow={this.props.answerShow}
          size={this.props.size}
          xIndex={this.props.xIndex}
          yIndex={this.props.yIndex}
          timer={this.state.timer}
          goal={this.props.goal}
        />
      </div>
    );
  }
}

export default Timer;
